:root {
  font-family: Open Sans, sans-serif;
}

#sketch {
  width: min-content;
  align-items: center;
  margin: 25px;
  display: inline-block;
}

#sketch canvas {
  margin: auto;
  display: block;
}

#settings-bb {
  float: right;
  width: calc(50% - 425px);
  margin-top: 50px;
  margin-bottom: 50px;
}

#settings {
  width: 300px;
  height: max-content;
  text-align: center;
  background-color: #fafafa;
  border: 1px solid #000;
  padding: 20px;
}

#statistics-bb {
  float: left;
  width: calc(50% - 425px);
  margin-top: 70px;
}

#statistics {
  float: right;
  width: 350px;
  height: max-content;
  text-align: center;
  background-color: #fafafa;
  border: 1px solid #000;
  padding: 20px 20px 30px;
}

.settings-row, .statistics-row {
  justify-content: center;
  align-items: center;
  padding: 3px 0;
  display: flex;
}

.settings-row {
  gap: 10px;
}

.statistics-row {
  gap: 20px;
}

.settings-col-left, .statistics-col-left {
  width: 50%;
  text-align: right;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.settings-col-right, .statistics-col-right {
  text-align: left;
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.settings-col-right {
  width: 50%;
  flex: 50%;
}

.statistics-col-right, .statistics-col-split {
  width: 65%;
  flex: 65%;
}

.settings-switch {
  width: 16px;
  height: 16px;
  margin-left: 20px;
  display: inline-block;
}

.settings-number {
  width: 50px;
  margin-left: 20px;
  display: inline-block;
}

.control-label {
  margin-bottom: 0;
}

#settings-toggle {
  text-align: center;
  margin: 20px auto auto;
  padding: 1px 15px;
  display: block;
}

#settings-content {
  height: 450px;
  transition: height .25s ease-in-out;
  position: relative;
  overflow: hidden;
}

#settings-content.collapsed {
  height: 290px;
}

#basic-settings, #advanced-settings {
  width: 100%;
  transition: left .25s ease-in-out;
  position: absolute;
  left: 0;
}

#basic-settings.collapsed {
  left: -300px;
}

#basic-settings .settings-row, #advanced-settings .settings-row {
  padding: 5px 0;
}

#advanced-settings.collapsed {
  left: 300px;
}

#advanced-settings label {
  font-size: 85%;
}

#advanced-settings .settings-number, #advanced-settings .settings-switch {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 10px;
}

#advanced-settings .settings-switch {
  height: .9rem;
}

#advanced-settings .settings-number {
  height: 1.4rem;
  font-size: .85rem;
}

#advanced-settings .settings-col-left {
  flex: 85%;
}

#advanced-settings .settings-col-right {
  flex: 35%;
}

.statistics-number {
  font-family: monospace;
  font-size: 1.1rem;
}

.statistics-col-split {
  justify-content: center;
  align-items: center;
  display: flex;
}

.statistics-split-right {
  text-align: right;
  color: #555;
  flex: 20%;
}

.statistics-split-right > .statistics-number {
  white-space: nowrap;
  font-size: .9rem;
  display: inline-block;
}

.statistics-positive {
  color: green;
}

.statistics-negative {
  color: red;
}

#replay-btn-div {
  width: 100px;
  position: absolute;
  top: 400px;
  left: calc(50% + 195px);
}

#replay-btn {
  visibility: hidden;
}

@media screen and (max-width: 1575px) {
  #statistics-bb {
    float: right;
    margin-top: 0;
  }

  #settings, #statistics {
    float: left;
  }

  #settings-bb, #statistics-bb {
    width: 36%;
  }

  #sketch {
    float: left;
    width: 60%;
    margin-left: 25px;
  }

  #replay-btn-div {
    left: calc(30% + 220px);
  }
}

@media screen and (max-width: 1330px) {
  #settings-bb, #settings, #statistics-bb, #statistics {
    float: none;
  }

  #settings-bb, #statistics-bb {
    width: min-content;
    height: min-content;
    margin: 25px auto;
  }

  #sketch {
    float: none;
    width: 100%;
    margin: 25px auto;
    display: block;
  }

  #replay-btn-div {
    left: calc(50% + 195px);
  }
}

@media screen and (max-width: 820px) {
  #replay-btn-div {
    left: 595px;
  }
}

